body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

body, html, #root {
  height: 100%;
  min-width: 800px;
}

.App {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  background: #475167;
}

.App--Content {
  padding: 30px;
  overflow: hidden;
}

.App--Loading {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App--Loading i {
  color: white;
}

.App--Error {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App--Error i {
  color: white;
}

.App--Error h3 {
  color: white;
  text-align: center;
}

.App--Error button {
  background: white;
  color: #475167;
  padding: 10px 30px;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.App--Error button:hover {
  opacity: 1;
}

.Slider *:focus {
  outline: none;
}

.Slide--Content {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Slide--Content_inner {
  width: 800px;
}
.SnowReport {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: stretch;
}

.SnowReport--Current {
	display: flex;
	flex-direction: column;
	align-items: stretch;
}

.SnowReport--Current_Title {
	color: white;
	text-align: center;
}

.SnowReport--Current_Content {
	display: flex;
	flex-direction: row;
	align-self: stretch;
	align-items: center;
	justify-content: center;
}

.SnowReport--Current_section {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 0 40px;
}

.SnowReport--Current_section.align-left {
	align-items: flex-start;
	text-align: left;
}

.SnowReport--Current_section.align-right {
	align-items: flex-end;
	text-align: right;
}

.SnowReport--Current_section-label {
	color: white;
}

.SnowReport--Current_section-value {
	color: white;
	font-size: 28px;
	font-weight: bold;
}

.SnowReport--Current_icon {
	color: white;
}

.SnowReport--Current_Extras {
	margin-top: 50px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

.SnowReport--Current_Extras-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	flex: 1 1;
}

.SnowReport--Current_Extras-item_label {
	color: white;
	font-weight: bold;
	border-bottom: 1px solid white;
	padding: 5px 10px;
}
.SnowReport--Current_Extras-item_value {
	color: white;
	font-size: 22px;
	font-style: italic;
	padding: 10px;
}

.SnowReport--Forecast {
	display: flex;
	flex-direction: column;
	align-items: stretch;
}

.SnowReport--Forecast_Title {
	text-align: center;
	color: white;
}

.SnowReport--Forecast_Row {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	padding: 20px;
}

.SnowReport--Forecast_Row-label {
	color: white;
	font-size: 18px;
	text-transform: uppercase;
}

.SnowReport--Forecast_Row-label strong {
	margin-right: 5px;
}

.SnowReport--Forecast_Row-value {
	color: white;
	font-size: 28px;
}
